<template>
  <div class="special-project full-screen">
    <div class="page-panel">
      <pem-scroll-view
        ref="scroll"
        class="scroll-view"
        :probeType="probeType"
        :data='list'
        :pullup=true
        :pulldown=true
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">下拉刷新...</div>
          <div class="special-top">
            <div class="special-bg">
              <img :src="special.logo"  @error="defaultLogo"/>
              <h1>{{ special.title }}</h1>
              <h3>{{ `项目数量：${special.count}` }}</h3>
            </div>
          </div>
          <div class="special-desc" v-if="special.desc">
            {{ special.desc }}
            <span v-if="isShowBtn" class="special-show-btn" @click="showAll">{{ isShowAll ? '收起' : '展开' }}</span>
          </div>
          <div class="special-project-list">
            <pem-project-list :data="list" @alert="alert"/>
          </div>
          <div class="scroll-view-pullup">目前就这么多了</div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
    <pem-dialog ref='alert' :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemProjectList from 'components/pem-project-list/pem-project-list'
import PemDialog from 'components/dialog/dialog'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle } from 'common/js/util'

import tempLogoUrl from 'common/image/icon-project-Default_graph@2x.png'

export default {
  components: {
    PemOperationPanel,
    PemScrollView,
    PemProjectList,
    PemDialog,
    PemLoading
  },
  data () {
    return {
      special: {
        title: '',
        desc: '',
        logo: null,
        count: 0
      },
      tempDesc: '',
      descLength: 66,
      isShowAll: false,
      isShowBtn: false,
      list: [],
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isLoading: true
    }
  },
  created () {
    this.probeType = 3
    this.listenScroll = true
    this._initWXShare()
    this._setTitle('投融资信息对接服务平台')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: this.special.title || '为您推荐项目工场',
        content: this.special.desc || '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        imgUrl: this.special.logo || 'http://m.pemarket.com.cn/static/logo.png'
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getMarketDetail()
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    listRefresh () {
      if (!this.isLoading) {
        this.getMarketDetail()
      }
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || '确定'
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    showAll () {
      if (!this.isShowAll) {
        this.isShowAll = true
        this.special.desc = this.tempDesc
      } else {
        this.isShowAll = false
        this.special.desc = this.tempDesc.substr(0, this.descLength) + '...'
      }
      setTimeout(() => {
        this.$refs.scroll.refresh()
      }, 50)
    },
    getMarketDetail () {
      this.isLoading = true
      let options = {channelzj: this.$route.params.zj.toString()}
      if (this.$route.query.fapp) options['shareid'] = this.$route.query.fapp
      this._getMarketDetail(options, (data) => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.list = data.resultList.filter(item => {
            if (item.logo) {
              item.logo = ImgPath + item.logo
            }
            if (item.iconList) {
              item.iconList = item.iconList.map(icon => {
                icon.imgurl = ImgPath + icon.imgurl
                return icon
              })
            }
            if (item.layout === 'line') {
              return false
            }
            if (item.layout === 'text') {
              this.special.desc = item.text
              this.tempDesc = item.text || ''
              if (this.tempDesc.length <= this.descLength) {
                this.special.desc = this.tempDesc
              } else {
                this.special.desc = this.tempDesc.substr(0, this.descLength) + '...'
                this.isShowBtn = true
              }
              return false
            }
            if (item.marketTitle) {
              this.special.title = item.marketTitle
              return false
            }
            if (item.layout === 'imagetext') {
              return false
            }
            if (item.layout === 'banner') {
              this.special.logo = item.banner.length > 0 ? (ImgPath + item.banner[0].uri) : tempLogoUrl
              this.special.count = item.projectcount || 0
              return false
            }
            return true
          })
        }
        this._initWXShare()
      })
    },
    _getMarketDetail (options, callback) {
      MiniSiteApi.getMarketDetail(options).then(data => {
        callback(data)
      })
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-project-Default_graph@2x.png')
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .special-project
    font-size: 0.24rem
    .page-panel
      top: 0
    .special-top
      position: relative
      z-index: -1
      top: 0
      left: 0
      height: 3.18rem
      width: 100%
    .special-bg
      position: absolute
      width: 100%
      height: 4.16rem
      top: -0.98rem
      text-align: center
      bg-image('../../../common/image/minisite/special/special-project-bg')
      background-size: cover
      img
        width: 1.12rem
        height: 1.12rem
        border-radius: 4px
        margin-top: 1.44rem
        margin-bottom: 0.2rem
      h1
        font-size: 0.48rem
        line-height: 0.66rem
        color: #FFFFFF
        letter-spacing: -0.39px
      h3
        font-size: 0.24rem
        color: #FFFFFF
        letter-spacing: -0.2px
        line-height: 0.34rem
    .special-desc
      padding: 0.3rem
      background: $color-panel
      font-size: 0.28rem
      line-height: 0.45rem
      color: $color-text-body
      border-bottom: 0.1rem solid #f6f6f6
      word-break: break-all
      .special-show-btn
        font-size: 0.24rem
        color: #4D95FF
        letter-spacing: -0.09px
        line-height: 0.4rem
</style>



